/** @format */

import React from "react";

const Aboutimages = (props) => {
    return (
        <>
            <img
                srcSet={`${props.image} 300w, ${props.imageLarge} 1000w`}
                sizes="(max-width: 56.25em) 20vw, (max-width: 37.5em) 30vw, 300px"
                alt={props.altText}
                className={`composition__photo ${props.cla}`}
                src={props.imageLarge}
            />
        </>
    );
};

export default Aboutimages;
