/** @format */

import React from "react";

const Navbaritems = (props) => {
    return (
        <>
            <li className="navigation__item">
                <a href="#About" className="navigation__link">
                    <span>{props.num}</span>
                    {props.desc}
                </a>
            </li>
        </>
    );
};

export default Navbaritems;
