/** @format */

import React from "react";
import Footeritems from "./components/Footeritems";
import Images from "../../img/Img";

const Footer = () => {
    return (
        <>
            <footer className="footer">
                <div className="footer__logo-box">
                    <picture className="footer__logo">
                        <source
                            srcSet={`${Images.logo_green_1x} 1x, ${Images.logo_green_2x} 2x`}
                            media="(max-width: 37.5em)"
                        />
                        <img
                            srcSet={`${Images.logo_green_1x} 1x, ${Images.logo_green_2x} 2x`}
                            alt="Full logo"
                            src={Images.logo_green_1x}
                        />
                    </picture>
                </div>
                <div className="row">
                    <div className="col-1-of-2">
                        <div className="footer__navigation">
                            <ul className="footer__list">
                                <Footeritems content="Company" />
                                <Footeritems content="Contact us" />
                                <Footeritems content="Carrers" />
                                <Footeritems content="Privacy policy" />
                                <Footeritems content="Terms" />
                            </ul>
                        </div>
                    </div>
                    <div className="col-1-of-2">
                        <p className="footer__copyright">
                            Built by{" "}
                            <a href="#Jonas" className="footer__link">
                                Jonas Schmedtmann
                            </a>{" "}
                            for his online course{" "}
                            <a href="#CSS" className="footer__link">
                                Advanced CSS and Sass
                            </a>
                            . Copyright &copy; by Jonas Schmedtmann. You are
                            100% allowed to use this webpage for both personal
                            and commercial use, but NOT to claim it as your own
                            design. A credit to the original author, Jonas
                            Schmedtmann, is of course highly appreciated!
                        </p>
                    </div>
                </div>
            </footer>
        </>
    );
};

export default Footer;
