/** @format */

import React from "react";
import Personstories from "./components/Personstories";
import Images from "../../img/Img";

const Stories = () => {
    return (
        <>
            <section className="section-stories">
                <div className="bg-video">
                    <video className="bg-video__content" autoPlay muted loop>
                        <source src={Images.video_mp4} type="video/mp4" />
                        <source src={Images.video_webm} type="video/webm" />
                        Your browser is not supported!
                    </video>
                </div>

                <div className="u-center-text u-margin-bottom-big">
                    <h2 className="heading-secondary">
                        We make people genuinely happy
                    </h2>
                </div>

                <Personstories
                    image={Images.nat_8}
                    caption="Mary Smith"
                    tert="I had the best week ever with my family"
                    text="Lorem ipsum dolor sit amet consectetur
                                    adipisicing elit. Aperiam, ipsum sapiente
                                    aspernatur libero repellat quis consequatur
                                    ducimus quam nisi exercitationem omnis earum
                                    qui. Aperiam, ipsum sapiente aspernatur
                                    libero repellat quis consequatur ducimus
                                    quam nisi exercitationem omnis earum qui."
                />

                <Personstories
                    image={Images.nat_9}
                    caption="Jack Wilson"
                    tert="WOW! My life is completely different now"
                    text="Lorem ipsum dolor sit amet consectetur
                        adipisicing elit. Aperiam, ipsum sapiente
                        aspernatur libero repellat quis consequatur
                        ducimus quam nisi exercitationem omnis earum
                        qui. Aperiam, ipsum sapiente aspernatur
                        libero repellat quis consequatur ducimus
                        quam nisi exercitationem omnis earum qui."
                />

                <div className="u-center-text u-margin-top-huge">
                    <a href="#Readstories" className="btn-text">
                        Read all stories &rarr;
                    </a>
                </div>
            </section>
        </>
    );
};

export default Stories;
