/** @format */

import React from "react";

const Tourscard = (props) => {
    return (
        <>
            <div className="col-1-of-3">
                <div className="card">
                    <div className="card__side card__side--front">
                        <div
                            className={`card__picture card__picture--${props.num}`}
                        >
                            &nbsp;
                        </div>
                        <h4 className="card__heading">
                            <span
                                className={`card__heading-span card__heading-span--${props.num}`}
                            >
                                {props.head}
                            </span>
                        </h4>
                        <div className="card__details">
                            <ul>
                                <li>{props.list.list1}</li>
                                <li>{props.list.list2}</li>
                                <li>{props.list.list3}</li>
                                <li>{props.list.list4}</li>
                                <li>{props.list.list5}</li>
                            </ul>
                        </div>
                    </div>
                    <div
                        className={`card__side card__side--back card__side--back-${props.num}`}
                    >
                        <div className="card__cta">
                            <div className="card__price-box">
                                <p className="card__price-only">Only</p>
                                <p className="card__price-value">
                                    {props.price}
                                </p>
                            </div>
                            <a href="#popup" className="btn btn--white">
                                Book now!
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default Tourscard;
