/** @format */

import React from "react";
import Aboutheading from "./components/Aboutheading";
import Aboutimages from "./components/Aboutimages";
import Images from "../../img/Img";

const About = () => {
    return (
        <>
            <section className="section-about">
                <div className="u-center-text u-margin-bottom-big">
                    <h2 className="heading-secondary">
                        Exciting tours for adventurous people
                    </h2>
                </div>

                <div className="row">
                    <div className="col-1-of-2">
                        <Aboutheading
                            tert="You're going to fall in love with nature"
                            para="Lorem ipsum dolor sit amet consectetur
                                adipisicing elit. Aperiam, ipsum sapiente
                                aspernatur libero repellat quis consequatur
                                ducimus quam nisi exercitationem omnis earum
                                qui."
                        />

                        <Aboutheading
                            tert="Live adventures like you never have before"
                            para="Lorem ipsum dolor sit amet consectetur
                                adipisicing elit. Asperiores nulla deserunt
                                voluptatum nam."
                        />

                        <a href="#Learnmore" className="btn-text">
                            Learn more &rarr;
                        </a>
                    </div>
                    <div className="col-1-of-2">
                        <div className="composition">
                            <Aboutimages
                                image={Images.nat_1}
                                imageLarge={Images.nat_1_large}
                                altText="Pic 1"
                                cla="composition__photo--p1"
                            />
                            <Aboutimages
                                image={Images.nat_2}
                                imageLarge={Images.nat_2_large}
                                altText="Pic 2"
                                cla="composition__photo--p2"
                            />
                            <Aboutimages
                                image={Images.nat_3}
                                imageLarge={Images.nat_3_large}
                                altText="Pic 3"
                                cla="composition__photo--p3"
                            />
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

export default About;
