/** @format */

import React from "react";
import Tourscard from "./Tourscard";

const tourcardList = [
    {
        list1: "3 day tours",
        list2: "Up to 30 people",
        list3: "2 tour guides",
        list4: "Sleep in cozy hotels",
        list5: "Difficulty: easy",
    },
    {
        list1: "7 day tours",
        list2: "Up to 40 people",
        list3: "6 tour guides",
        list4: "Sleep in provided tents",
        list5: "Difficulty: medium",
    },
    {
        list1: "5 day tours",
        list2: "Up to 15 people",
        list3: "3 tour guides",
        list4: "Sleep in provided tents",
        list5: "Difficulty: hard",
    },
];

const Tour = () => {
    return (
        <>
            <section className="section-tours" id="section-tours">
                <div className="u-center-text u-margin-bottom-big">
                    <h2 className="heading-secondary">Most popular tours</h2>
                </div>

                <div className="row">
                    <Tourscard
                        num="1"
                        head="The Sea Explorer"
                        price="$297"
                        list={tourcardList[0]}
                    />
                    <Tourscard
                        num="2"
                        head="The Forest Hiker"
                        price="$497"
                        list={tourcardList[1]}
                    />
                    <Tourscard
                        num="3"
                        head="The Snow Adventurer"
                        price="$897"
                        list={tourcardList[2]}
                    />
                </div>

                <div className="u-center-text u-margin-top-huge">
                    <a href="#Discover" className="btn btn--green">
                        Discover all tours
                    </a>
                </div>
            </section>
        </>
    );
};

export default Tour;
